.content-info {
	border-top: solid 1px $gtf-teal;
	padding-top: 3.6em;
	padding-bottom: 3.6em;
	.container-fluid {
		width: 91.8%;
	}
	h3 {
		text-transform: uppercase;
		font-size: 1.2em;
		margin-bottom: 0.2em;
	}
	p, address, a {
		font-size: 1.2em;
	}
	p {
		margin-bottom: 0.2em;
	}
	#loginform {
		label {
			display: block;
			font-weight: normal;
		}
		input[type=text], input[type=password] {
			@include box-sizing(border-box);
			-webkit-appearance: none;
			@extend .form-control;
			height: 23px;
			border-width: 2px;
			margin-bottom: 0.9em;
		}
		#wp-submit {
			@extend .btn;
			@extend .btn-large;
			@extend .btn-primary;
			height: auto;
			font-size: 0.85em;
		}
	}
	[id^='menu-social'] {
		@extend .nav;
		@extend .navbar-nav;
		text-transform: uppercase;
		margin-left: -10px;
		font-size: 0.75em;
		&>li {
			&:before {
				top: -2px;
				color: $text-color;
			}
			&>a {
				padding: 0 9px;
				color: $text-color;
				line-height: 15px;
				&:hover, &:active {
					background-color: transparent;
				}
				.smil & {
					line-height: 15px;
				}
			}
		}
		.menu-contact-us {
			display: none;
		}
	}
	form {
		margin-bottom: 1.2em;
	}
	input {
		height: 23px;
		border-width: 2px;
	}
	.search-field {
		width: 65%;
		margin-right: 0.5em;
	}
	.widget_search {
		padding-top: 1.7em;
		padding-left: 2.8em;
		h3 {
			text-transform: inherit;
			margin-bottom: 0;
		}
	}
	.widget_nav_menu {
		padding-left: 2.8em;
	}
}