.image-with-overlayed-text {
	margin: 0 -15px 5.7em;
	font-size: 14px;
	position: relative;
	overflow:hidden;
	img {
		@include img-responsive();
	}
	.carousel-caption {
		//width: 64.9%;
		//top: 41.9%;
		h1 {
			//font-size: 3.1em;
		}
	}
}

.main {
	.carousel-caption {
		position: absolute;
		top: 50%;
		padding: 0;
		text-align: left;
		width: 100%;
		@include transform(translateY(-50%));
		left:0;
		right:0;
		bottom:0;
		height: 100%;
		.floating {
			padding-left: 5px;
			padding-right: 5px;
			position: absolute;
			top: 50%;
			@include transform(translateY(-50%));
			right: 5%;
		}
		h1 {
			margin-top: 0;
			padding: 0;
		}
		h1, h3 {
			text-transform: uppercase;
			text-align: left;
			color: #fff;
			font-size: 3.6em;
			line-height: 1.3;
			margin-bottom: 0;
			font-weight: 700;
		}
		a, p {
			color: $gtf-gold;
			text-transform: uppercase;
			font-size: 2.15em;
		}
	}
	.carousel-indicators {
		bottom: -4px;
		li {
			width: 14px;
			height: 14px;
			@include border-radius(14px);
			margin: 2px;
			position: relative;
			&.active {
				background-color: transparent;
				&:after {
					content: '';
					position: absolute;
					top:2px;
					left: 2px;
					width: 8px;
					height: 8px;
					@include border-radius(8px);
					background-color: #fff;
				}
			}
		}
	}
}

