.btn {
	border-width: 3px;
	text-transform: uppercase;
	letter-spacing: 3px;
	.smil & {
		padding-bottom: 4px;
	}
}

#facts {
	.btn {
		font-size: 1.8em;
		margin-top: 0.5em;
	}
}

#donate {
	border-bottom: solid 7px #fff;
	.btn {
		font-size: 1.8em;
		margin-top: 0.8em;
		width: 80%;
		min-width: 185px;
		@include button-variant(#fff, $gtf-teal, #fff);
		@include border-radius(0);
	}
}

#team, #donation-info, #statistics {
	.btn {
		font-size: 1.8em;
		@include button-variant($gtf-gold, #fff, $gtf-gold);
	}
}

#donation-info {
	.btn {
		font-size: 1.8em;
		@include button-variant($gtf-green, #fff, $gtf-green);
		margin-top: .5em;
		letter-spacing: 2px;
	}
}

#statistics {
	.btn {
		margin-top: 1.1em;
		padding-left: 40px;
		padding-right: 40px;
	}
}

#our-mission {
	.btn {
		font-size: 1.8em;
		@include button-variant($gtf-teal, #fff, $gtf-teal);
		margin-top: .3em;
		padding-left: 25px;
		padding-right: 25px;
	}
}

#team-listing {
	.btn {
		font-size: 1.7em;
		margin-top: 0.4em;
	}
}

.blog {
	.main {
		article {
			.btn {
				font-size: 2.1em;
				@include button-variant($gtf-gold, #fff, $gtf-gold);
			}
		}
	}
}

.search-submit {
	@include button-variant($text-color, #fff, $gtf-inputs);
	@include border-radius(0);
	padding: 2px 12px;
	font-size: 0.9em;
	letter-spacing: 1px;
	border-width: 2px;
	height: 23px;
	width: 7.2em;
}

.widget, .content-info {
	.btn-primary, #loginform #wp-submit {
		letter-spacing: 1px;
		height: 32px;
		@include border-radius(0);
		font-size: 1.05em;
		padding: 4px 6px;
		line-height: 1.3;
		margin-bottom: 1em;
		.smil & {
			padding-top: 6px;
		}
	}
}