@media (max-width: 875px) {
	#menu-navigation > li > a {
		padding-left: 1.5em;
		padding-right: 1.5em;
	}
}

@media (max-width: 835px) {
	.carousel-inner > .item > img {
		width: 820px;
		height: 345px;
		max-width: none;
	}
}

@media (max-width: 825px) {
	#trauma-center-list {
		font-size: 13px;
	}
}

@media (max-width: 795px) {
	#trauma-center-list {
		font-size: 12px;
	}
}

@media (max-width: 767px) {
	.about-us .main, .your-trauma-center .main {
		padding-top: 1.5em;
		h1 {
			letter-spacing: 0;
			font-size: 2.8em;
		}
		p {
			padding: 0 5%;
		}
	}
	#our-vision h3 {
		padding: 0 1%;
	}
	.vertical-align-flex {
		display: block;
	}
	[id^=menu-social] {
		float: left;
		margin: 0 -15px 0;
		> li {
			float: left;
		}
	}
	.navbar > .container-fluid .navbar-brand {
		background: url(../images/logo-gtf.svg) center 8px no-repeat;
		height: 81px;
	}
	.navbar-header {
		height: 80px;
	}
	#menu-navigation {
		margin: 0;
	}
	.content-info {
		padding-top: 1.6em;
		padding-bottom: 1.6em;
		.widget {
			text-align: center;
		}
		.widget_search {
			padding-top: 0;
			padding-left: 0;
		}
		.search-field {
			width: 100%;
			margin-right: 0;
			margin-bottom: .5em;
		}
	}
	.search-submit, .widget .btn-primary {
		width: 100%;
	}
	.widget_nav_menu {
		display: none;
	}
	#our-mission .btn {
		white-space: normal;
	}
	#team-listing {
		.btn {
			white-space: normal;
			@include center-block();
		}
		.person p {
			padding: 0;
		}
	}
	.xs-width-12 {
		width: 100%;
	}
	#locations h3 {
		margin-top: 5%;
	}
	#locations #tabpanel .tab-content ul {
		@include column-count(1);
		padding-left: 5%;
		padding-right: 5%;
		text-align: center;
	}
	#teammembers {
		.col-sm-3:nth-child(4) {
			&:after {
				padding-bottom: 2.2em;
			}
		}
	}
	#elaine-frantz-rn-bsm-ma .col-sm-3:after {
		top: 50%;
	}
}

@media (max-width: 750px) {
	.image-with-overlayed-text {
