	#trauma-center-list,
	#tab-content-addresses {
		width: 100%;
	}
	#trauma-center-list {
		padding-left: 15px;
		padding-bottom: 5%;
	}
	#locations {
		h3 {
			text-align: center;
		}
		.nav-tabs {
