.single-teammember {
	.article {
		padding-bottom: 2.1em;
	}
	#heading-image {
		margin-bottom: 4.2em;
		.carousel-caption {
			width: auto;
			//top: 57.9%;
			//left: 4%;
			.floating {
				left: 5%;
				right: inherit;
			}
			h1 {
				font-size: 3em;
			}
		}
	}
	.entry-content {
		h2 {
			margin-bottom: 0.8em;
		}
		p {
			text-align: center;
			font-size: 1.3em;
			padding: 0 8%;
			line-height: 1.9;
			margin-bottom: 2.1em;
		}
	}
	blockquote {
		border-left: none;
		margin: -0.2em 0 0;
		padding: 0 8%;
		p {
			color:$gtf-gold;
			font-style: italic;
			font-size: 1.5em;
			line-height: 1.4;
			margin-bottom: 1.4em;
		}
	}
	#donate {
		margin-bottom: -0.6em;
	}
}

.blog {
	#heading-image {
		margin-bottom: 4.9em;
		.carousel-caption {
			//width: 66.9%;
			.floating {
				top: 40%;
			}
			h1 {
				color:$gtf-dark-green;
				font-size: 3.1em;
				line-height: 1.6;
				@include text-shadow(rgba(#fff, 1) 0 0 7px, rgba(#fff, 1) 0 0 7px, rgba(#fff, 1) 0 0 7px);
			}
		}
	}
	.main {
		p {
			padding: 0 10%;
		}
		h2 {
			color:$gtf-teal;
			font-size:2.1em;
			text-align: left;
			padding: 0;
		}
		article {
			padding: 0 5.4% 0 8.6%;
			margin-top: 4.7em;
			margin-bottom: 5.4em;
			time {
				text-transform: uppercase;
				color:$gtf-dark-green;
				font-size: 1.5em;
				font-weight: 700;
			}
			p {
				text-align: left;
				padding: 0;
			}
		}
	}
}

.post-type-archive-document, .search {
	.main {
		margin-bottom: .5em;
		margin-top: 1em;
		article {
			width: 75%;
			margin: 0 auto;
			padding-bottom: 2.5em;
			padding-top: .9em;
			border-top: 1px solid $gtf-dark-green;
		}
		h2 {
			font-size: 1.5em;
		}
		.updated {
			text-align: center;
			display: block;
		}
		.btn {
			margin: 0 auto;
			@include center-block();
			width: 40%;
		}
		img {
			float: left;
			position: absolute;
		}
	}
}