.main {
	h1,h2 {
		text-transform: uppercase;
		text-align: center;
		font-weight: 700;
		color:$gtf-dark-green;
		font-size: 3.3em;
		padding: 0 1%;
	}
	h2 {
		font-size: 3.15em;
	}
	p {
		font-size: 1.3em;
		padding: 0 8%;
		line-height: 1.9;
		text-align: center;
	}
}

#facts {
	text-align: center;
	margin-left: 2em;
	margin-right: 2em;
	margin-bottom: 5.7em;
	img {
		margin-top: 0.5em;
		margin-bottom: 1em;
		@include img-responsive();
		@include center-block();
		max-width: 70%;
	}
	p {
		font-size: 1.3em;
		line-height: 1.8;
		padding: 0;
	}
}

#donate {
	background-color: $gtf-dark-green;
	padding-left: 7%;
	padding-right: 7%;
	padding-top: 2.6em;
	padding-bottom: 2.2em;
	p {
		color: #fff;
		text-transform: uppercase;
		font-size: 1.2em;
		font-weight: 600;
		text-align: left;
		padding: 0;
	}
}

#story {
	margin: 0 -15px 4.3em;
	font-size: 14px;
	position: relative;
	overflow: hidden;
	.carousel-caption {
		//top:48.9%;
		//width: 72.9%;
		.floating {
			h1 {
				color:$gtf-dark-green;
				font-size: 3.2em;
				font-weight: normal;
				text-align: right;
				line-height: 1.5;
				@include text-shadow(rgba(#fff, 1) 0 0 7px, rgba(#fff, 1) 0 0 7px);
			}
			a {
				//@include text-shadow(rgba(#fff, 1) 0 0 7px, rgba(#fff, 1) 0 0 7px);
				text-align: right;
				display: block;
			}
		}
	}
	img {
		@include img-responsive();
	}
}

.stamp {
	position: absolute;
	bottom: -21%;
	left: 6%;
	font-size: 30px;
	width: 8em;
	height: 8em;
	@include transform(rotate(-18deg));
}

#team {
	text-align: center;
	padding-bottom: 4.4em;
	.btn {
		white-space: normal;
	}
}

#teammembers {
	padding-right: 2em;
	padding-left: 2em;
	margin-top: 1em;
	img {
		@extend .img-circle;
		margin-bottom: 1.9em;
		@include img-responsive();
		@include center-block();
	}
	p {
		text-transform: uppercase;
		font-size: 1.2em;
		padding: 0;
		//white-space:nowrap;
	}
}

#location {
	background-color: $gtf-dark-green;
	padding: 2.2em 1.4em 1.7em 2.6em;
	margin-bottom: 1.5em;
	h2 {
		color:#fff;
		text-align: left;
		font-size: 3em;
	}
	p {
		color:#fff;
		padding: 0;
		text-align: left;
	}
	a {
		color:$gtf-gold;
		text-transform: uppercase;
		font-size: 2.15em;
		line-height: 1.4;
		display: block;
		margin-bottom: 1em;
	}
	img + p {
		text-align: center;
		text-transform: uppercase;
		font-size: 1.3em;
		margin-top: -0.3em;
		padding-right: 1em;
	}
}

.donate {
	.main {
		h2 {
			font-size: 2.5em;
			color: $gtf-teal;
			line-height: 1.5;
			letter-spacing: 1.2px;
		}
	}
}

#donation-info {
	text-align: center;
	margin: 2.6em 1em 4.6em;
	h3 {
		text-transform: uppercase;
		font-weight: 700;
		color:$gtf-green;
		font-size: 2.1em;
		margin-bottom: .1em;
	}
	p {
		font-size: 1.2em;
		letter-spacing: 2px;
		line-height: 2.1;
		padding: 0;
	}
}

#footer-text {
	text-align: center;
	text-transform: uppercase;
	margin-bottom: 2em;
	h3 {
		margin-bottom: .2em;
	}
	p {
		line-height: 2.3;
		font-size: 1.1em;
	}
}

.why-trauma {
	#heading-image {
		margin-bottom: 4em;
	}
	.main {
		text-align: center;
		h1 {
			font-size: 2.5em;
			color: $gtf-dark-green;
			line-height: 1.5;
			letter-spacing: 1.2px;
			margin-bottom: 0.2em;
		}
	}
}

#icons {
	padding: 0 2em 3.8em;
	h3 {
		text-transform: uppercase;
		font-weight: 700;
		font-size: 1.4em;
		color: $gtf-dark-green;
	}
	img {
		@include img-responsive();
		@include center-block();
		max-width:85%;
		margin-top: 1.8em;
		margin-bottom: 1.2em;
		& + p {
			text-transform: uppercase;
			color: $gtf-gold;
			font-family: 'BrandonGrotesque-Black';
			font-size: 1.1em;
			line-height: 1.6;
			margin-bottom: 1.3em;
			padding: 0;
		}
	}
}

#second-image, #third-image {
	position: relative;
	.carousel-caption {
		//top: 40.1%;
		.floating {
			left: 5%;
			right: inherit;
		}
		p {
			color:#fff;
			text-transform: inherit;
			padding: 0;
			line-height: 1.2;
			text-align: left;
		}
	}
}

#second-image {
	margin-bottom: 4em;
}

#third-image {
	.carousel-caption {
		top: 42.1%;
		left: inherit;
		right: 5%;
		.floating {
			float: right;
		}
		h3, p {
			text-align: right;
		}
	}
}

#statistics {
	padding-left: 2em;
	padding-right: 2em;
	padding-bottom: 5.6em;
	text-align: center;
	h3 {
		color:$gtf-gold;
		font-size: 5em;
		//font-weight: 700;
		font-family: 'BrandonGrotesque-Black';
		text-transform: uppercase;
		margin-bottom: 0;
		line-height: 0.9;
		sup {
			font-size: 45%;
			font-family: 'BrandonGrotesque-Regular';
			top: -0.8em;
			font-weight: 700;
		}
	}
	p {
		margin-top: 0;
		padding: 0;
		font-size: 1.3em;
		letter-spacing: 2px;
		line-height: 2.1;
	}
}

#third-image {
	margin-bottom: 0;
}

.about-us {
	.main {
		padding-top: 4em;
		h1 {
			font-size: 3.0em;
			letter-spacing: 0.8px;
			margin-bottom: .4em;
		}
		p {
			padding: 0 11%;
		}
	}
}

#our-vision, #our-mission, #meet-our-team {
	text-align: center;
	padding-top: 1.3em;
	padding-bottom: 1.3em;
	h2 {
		font-size: 2.6em;
	}
	h3 {
		font-size: 1.3em;
		margin-top: 0.7em;
	}
	.icon-container {
		padding-left: 2em;
		padding-right: 2em;
		margin-top: 2.4em;
		img {
			@include img-responsive();
			@include center-block();
			max-width: 180px;
			max-height: 180px;
			margin-bottom: 1.2em;
		}
		p {
			padding: 0;
			margin-bottom: 1.1em;
		}
	}
}

#our-vision {
	background-color: $gtf-teal;
	position: relative;
	margin-top: 4.9em;
	h2 {
		color:#fff;
	}
	h3 {
		color: #fff;
		text-transform: uppercase;
	}
	.stamp {
		bottom: inherit;
		left: inherit;
		width: 6.5em;
		height: 6.5em;
		right: 8.7%;
		top: -5.3em;
		@include transform(rotate(17deg));
	}
	p {
		color:#fff;
	}
}

#our-mission {
	padding-top: 4.2em;
	padding-bottom: 4.8em;
	h2 {
		color: $gtf-dark;
	}
	.icon-container {
		p {
			text-transform: uppercase;
		}
	}
}

.main {
	#meet-our-team {
		position: relative;
		h2 {
			@extend .carousel-caption;
			color:#fff;
			position: absolute;
			left:0;
			right: 0;
			top: 50%;
			padding: 0;
			margin: 0;
			bottom: inherit;
			@include transform(translateY(-50%));
			font-size: 3.2em;
			text-align: inherit;
			height: auto;
		}
}
}

#team-listing {
	h3,h4 {
		text-transform: uppercase;
	}
	.person {
		padding-left: 2em;
		padding-right: 2em;
		margin-top: 4.5em;
		margin-bottom: 6em;
		h3 {
			color:$gtf-dark-green;
			font-weight: 700;
			font-size: 3em;
			margin-top: 0;
			line-height: 1;
		}
		h4 {
			color:$gtf-gold;
			font-weight: 700;
			font-size: 1.5em;
		}
		p {
			text-align: left;
			padding: 0 5em 0 0;
		}
		img {
			@extend .img-circle;
			@include img-responsive();
			@include center-block();
			width: 76%;
			height: 76%;
			margin-bottom: 10%;
			max-width:300px;
		}
		.btn {
			margin-bottom: .9em;
		}
	}
}

