	.col-sm-3:nth-child(4) {
		&:after {
			position: absolute;
			content: 'Image\A Coming\A Soon';
			left:0;
			right:0;
			top:50%;
			text-align: center;
			white-space: pre;
			font-size:1.7em;
			@include transform(translateY(-50%));
			line-height: 1.3;
			padding-bottom: 3.9em;
		}
	}
}

.your-trauma-center {
	.main {
		padding-top: 4em;
		h1 {
			font-size: 3.0em;
			letter-spacing: 0.8px;
			margin-bottom: .5em;
		}
	}
	#statistics {
		margin-top: 5%;
		padding-bottom: 3%;
		p {
			text-transform: uppercase;
		}
	}
	#donate {
		background-color: #fff;
		p {
			color:$gtf-dark-green;
		}
		.btn {
			@include button-variant($gtf-gold, #fff, $gtf-gold);
		}
	}
}

#locations {
	background-color: $gtf-teal;
	margin-top: 2em;
	padding-top: 2em;
	h2, p, h3 {
		color:#fff;
	}
	p {
		padding: 0 16%;
	}
	h3 {
		text-transform: uppercase;
		font-weight: 700;
		font-size: 1.5em;
		margin-top: 10%;
		//padding-left: 19%;
		letter-spacing: 1px;
	}
	#tabpanel {
		margin-top: 1.3em;
		img {
			@include img-responsive();
			@include center-block();
			padding-bottom: 2em;
		}
		.tab-content {
			ul {
				@include column-count(2);
				padding-right: 0;
				background-color: #fff;
				margin-bottom: 0;
				list-style: none;
				color:$gtf-teal;
				font-size: 1.2em;
				font-weight: 700;
				letter-spacing: 1px;
				padding-top: 3.9em;
				padding-left: 8.8%;
				border-bottom: 4em solid #fff;
				li {
					line-height: 1.8;
					margin-bottom: 2em;
					span {
						white-space: nowrap;
					}
				}
			}
		}
	}
	.nav-tabs {
		//padding-left: 19%;
		text-transform: uppercase;
		border-bottom: none;
		&>li {
			float:none;
			font-size: 1.6em;
			position: relative;
			&>a, &>a:focus, &>a:hover {
				z-index: 20;
				color:#fff;
				border:none;
				background-color: transparent;
				padding: 0 0 0 1.5em;
				line-height: 2.6;
			}
			&:before {
				z-index: 10;
				display: block;
				content:'';
				width: 20px;
				height: 20px;
				@include border-radius(20px);
				position: absolute;
				border: solid 2px #fff;
				top:50%;
				@include transform(translateY(-50%));
				vertical-align: middle;
				.smil & {
					top: 44%;
				}
			}
		}
		&>li.active {
			&>a, &>a:focus, &>a:hover {
				background-color: transparent;
				border:none;
			}
			&:after {
				z-index: 5;
				display: block;
				content:'';
				width: 20px;
				height: 20px;
				background-color: $gtf-gold;
				@include border-radius(20px);
				position: absolute;
				border: solid 6px $gtf-teal;
				top:50%;
				@include transform(translateY(-50%));
				vertical-align: middle;
				.smil & {
					top: 44%;
				}
			}
		}
	}
}

#trauma-center-list {
	padding-left: 10%;
}

.search {
	.main {
		.alert {
			margin-top: 1em;
			font-size: 2em;
			text-align: center;
		}
	}
}