body {
	font-family: 'BrandonGrotesque-Regular';
	overflow-x: hidden;
}

a:hover, a:focus {
	text-decoration: none;
}

.container-fluid {
	max-width: 1024px;
	min-width: 278px;
}

.vertical-align-flex {
	@include flexbox;
	@include align-items(center);
}

.pr {
	position: relative;
}

.pa {
	position: absolute;
}