.navbar-default {
	border-top: 30px solid $gtf-gray;
	margin-bottom: 0;
}

#social-menu {
	float: right;
	margin-top: -30px;
	text-transform: uppercase;
	font-weight: 700;
}

[id^='menu-social'] {
	&>li {
		&:before {
			display: block;
			content: "|";
			position: absolute;
			left: 0;
			top: 4px;
			color:$gtf-teal;
		}
		&:first-child:before {
			display: none;
		}
		&>a {
			padding: 0 8px;
			line-height: 30px;
			font-size: .9em;
			color:$gtf-teal;
			html.smil & {
				line-height: 33px;
				margin-bottom: -3px;
			}
			&:hover, &:active {
				color: darken($gtf-teal, 20%);
			}
		}
		&.menu-contact-us {
			a {
				color:$gtf-gold;
				&:hover, &:active {
					color: darken($gtf-gold, 20%);
				}
			}
		}
	}
}

#menu-navigation {
	text-transform: uppercase;
	margin: 98px auto 0;
	display: table;
	table-layout: fixed;
	float:none;
	font-weight: 700;
	&>li {
		//float:none;
		&>a {
			padding: 15px 2em 8px;
			font-size: 1.1em;
			color:$gtf-dark;
		}
	}
	&>.active {
		&>a {
			background-color: $gtf-teal;
		}
	}
}

.navbar-toggle {
	float: left;
	@include border-radius(0);
	margin-left: 8px;
	margin-right: 0;
}

.navbar {
	&>.container-fluid
	{
		.navbar-brand {
			clear: both;
			width: 100%;
			text-align: center;
			margin-left: 0;
			position: absolute;
			left:0;
			right: 0;
			@include text-hide();
			border-bottom: 1px solid $gtf-teal;
			background: url('../images/logo-gtf.svg') no-repeat center 13px;
			padding: 0;
			height: 99px;
		}
	}
}